import React, { useContext, useRef, useState } from 'react'
import 'remixicon/fonts/remixicon.css'
import MyContext from "./Context/MyContext";
import ReactPlayer from 'react-player';
import { useNavigate } from 'react-router-dom';

function VideoBox(props) {
    const context = useContext (MyContext);
    const {isPlaying,setIsPlaying,sendfunction}=context;
    const [plaubtn, setplaubtn] = useState("Play")
    const [hide, sethide] = useState({})
    const [style, setstyle] = useState()
    const [style2, setstyle2] = useState()
    const playerRef = useRef(null);
    const navigator = useNavigate();


    const handlePlayPause = (e) => {
      e.preventDefault();
      if(playerRef.current) {
        setIsPlaying(!isPlaying);
        if(isPlaying===false){
          setplaubtn("Play");
          setstyle({display:"flex",transition:"2s"})
        }else if(isPlaying===true){
          setplaubtn("Pause");
          if(plaubtn==="Play"){
            setTimeout(() => {
              setstyle({display:"none",transition:"2s"})
            }, 5000);
          }
        }
      }


      sendfunction({"isPlaying":!isPlaying})
      sethide({ opacity: 0})
    };

    const onMouseEnter = ()=>{
      if(isPlaying===true){
 
      }
    }

    const onMouseExit = ()=>{
      if(isPlaying===false){
       
      }
    }

    const onclickback = (e)=>{
        e.preventDefault();
        props.setvideolink(null)
        localStorage.removeItem("link")
    }


  return (
    <div onMouseEnter={onMouseEnter} onMouseLeave={onMouseExit} onMouse className='h-full w-full relative bg-white  flex  flex-col justify-center '>
      <div style={style} className='z-10 h-[7.5%] w-full absolute top-0 bg-black flex justify-start items-center px-5'> <button onClick={onclickback} className='text-white border-2 px-3 rounded-xl font-Roboto font-semibold'>Back</button></div>
      <button style={hide} onClick={handlePlayPause} className='z-20 absolute top-[50%] left-[50%] -translate-x-[50%] -translate-y-[50%] bg-black p-3 px-5 rounded-2xl border-4 text-white'>{plaubtn}</button>
    <ReactPlayer  ref={playerRef} url={localStorage.getItem("link")}  height="100%" width="100%"  volume={null} controls={false} playing={!isPlaying}  />  
    <div className='h-full w-full absolute'></div>
  <div style={style2} className='h-12 w-[85%] absolute px-5 bg-transparent flex items-center bottom-2 '>
    <button onClick={handlePlayPause} className='border rounded-xl px-2 text-white'>{plaubtn}</button>
</div>
  </div>
  )
}

export default VideoBox